@import url(https://fonts.googleapis.com/css2?family=DotGothic16&display=swap);
.button {
  position: relative;
  padding: 8px 16px;
  background-color: tomato;
  color: white;
  font-weight: bold;
  box-shadow: inset -4px -4px 0px 0px rgba(0, 0, 0, 0.2);
  cursor: pointer;
  font-family: 'DotGothic16', sans-serif;
  font-size: 20px;
  margin: 8px;
  -webkit-filter: contrast(0.8);
          filter: contrast(0.8);
}
.button::before, .button::after {
  position: absolute;
  width: 100%;
  height: 100%;
  box-sizing: content-box;
  content: "";
}
.button::before {
  top: -4px;
  left: 0;
  border-top: 4px var(--border-color) solid;
  border-bottom: 4px var(--border-color) solid;
}
.button::after {
  left: -4px;
  top: 0;
  border-left: 4px var(--border-color) solid;
  border-right: 4px var(--border-color) solid;
}
.button:hover {
  -webkit-filter: contrast(1);
          filter: contrast(1);
}
.button:active {
  box-shadow: inset 4px 4px 0px 0px rgba(0, 0, 0, 0.2);
  -webkit-filter: brightness(0.9);
          filter: brightness(0.9)
}
.horse-container {
  width: 35%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  cursor: pointer;
  border: 4px dashed var(--text-color);
  transition: all 0.2s;
}
.horse-container.active {
  border-color: #fa962a;
  color: #fa962a;
  background-color: #fa952a42;
}
.horse-container img {
  width: 70%;
  height: auto;
  -webkit-user-select: none;
      -ms-user-select: none;
          user-select: none;
}
.horse-container span {
  margin: 8px;
}
section#bet {

}
section#bet > header {
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  align-items: center;

  width: 100%;
}
section#bet > header .money {
  position: relative;
  display: flex;
  align-items: center;
  background-color: #ffb76a;
  height: 46px;
  color: white;
  font-weight: bold;
  box-shadow: inset -4px -4px 0px 0px rgba(0, 0, 0, 0.2);
}
section#bet > header .money .icon {
  display: flex;
  justify-content: center;
  align-items: center;
  border-right: 4px var(--border-color) solid;
  background-color: #de7c12;
  height: 100%;
  padding: 12px;
  font-size: 20px;
  box-shadow: inset 0px -4px 0px 0px rgba(0, 0, 0, 0.2);
}
section#bet > header .money span {
  padding: 14px;
}
section#bet > header .money::before,
section#bet > header .money::after {
  position: absolute;
  width: 100%;
  height: 100%;
  box-sizing: content-box;
  content: "";
}
section#bet > header .money::before {
  top: -4px;
  left: 0;
  border-top: 4px var(--border-color) solid;
  border-bottom: 4px var(--border-color) solid;
}
section#bet > header .money::after {
  left: -4px;
  top: 0;
  border-left: 4px var(--border-color) solid;
  border-right: 4px var(--border-color) solid;
}
section#bet .horse-list {
  flex: 1 1;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: space-evenly;
  align-items: center;
}
section#bet > footer form {
  flex: 1 1;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
}
section#bet > footer span {
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 24px;
  font-weight: bold;
  border: 4px solid var(--border-color);
  border-right: 0px;
  padding: 0px 12px;
  height: 53px;
  box-shadow: inset 0px -4px 0px 0px rgba(0, 0, 0, 0.2);
  background-color: #de7c12;
  color: white;
}
section#race div.sky {
  width: 100%;
  height: 40%;
  background-color: skyblue;
}
section#race div.ground {
  width: 100%;
  height: 60%;
  display: flex;
  flex-direction: column;
  justify-content: center;

  background-color: rgb(189, 142, 88);
}
section#race div.ground div.fance {
  width: 100%;
  background-repeat: repeat-x;
  background-size: auto 100%;
  height: 30px;
  -ms-interpolation-mode: nearest-neighbor;
      image-rendering: -moz-crisp-edges;
      image-rendering: pixelated;
}
section#race div.ground div.fance:last-child {
  position: absolute;
  bottom: 0px;
}
section#race div.ground div.track {
  background-color: green;
  border-top: 2px solid white;
  border-bottom: 2px solid white;
}
section#race div.ground div.track,
section#race div.ground div.horse-item
{
  height: 46px;
}
section#race div.ground div.racecourse {
  position: relative;
  overflow: hidden;
}
section#race div.ground div.horses {
  width: 100%;
  position: absolute;
  top: -10px;
  left: -40px;
}
section#race div.ground div.horse-item img {
  height: 100%;
}
section#race div.ground div.track:first-child {
  border-top: 0px solid white;
}
section#race div.ground div.track:last-child {
  border-bottom: 0px solid white;
}
#dialog-container {
  position: absolute;
  z-index: 5;

  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;

  max-width: 460px;
  width: 100%;
  padding: 32px 16px;
  background-color: var(--background-color);
  border: 4px dashed var(--text-color);
  color: var(--text-color);

  text-align: center;
}
#dialog-container strong {
  font-size: 22px;
}
#dialog-container p {
  margin: 16px 0px;
  font-size: 18px;
}
#dialog-container button {
  font-size: 16px;
}
#dialog-filter {
  -webkit-filter: brightness(0.9);
          filter: brightness(0.9);
  position: absolute;
  z-index: 4;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.6);
}
/* Reset */
* {
  margin: 0;
  padding: 0;
  border: 0;
  box-sizing: border-box;
  transition: background-color 0.3s;
  outline: none;
}

